html {
	background-image: url(../images/bg_full.jpg);
    background-repeat: no-repeat;
    background-position: 50% -1px;
    background-color: #000;

    font-family: 'Castrol', sans-serif;
}


@media screen and (orientation:landscape) { 
	.portrait-overlay{display:none;} 
}
@media screen and (orientation:portrait) { 
	.game-started.portrait-overlay{
		display:block !important;} 
	}


#score-table  {
    border-collapse: collapse;
    width: 100%;
}

#score-table th {
     background-color: #d4af79;
         color:#000;
}

tr{
	background: rgba(0,0,0,0.7);
}

.subtitle {
    color: #f1dbc6;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
}

p {
    display: block;
    margin: 1em 0;
}

.myscore {
     background-color: #d4af79;
}


#score-table td, th {
    border: 1px solid #ead1a5;
    text-align: left;
    padding: 8px;
}


#score-table td {
    color:#fff;
}


